nav {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 3fr 1fr;
      grid-template-columns: 1fr 3fr 1fr;
  -webkit-column-gap: 5px;
          grid-column-gap: 5px;
          column-gap: 5px;
  justify-items: center;
  background-color: rgba(174, 209, 255, 0.3);
}

nav a {
  text-decoration: none;
  color: black;
}

nav a:hover {
  color: #d484ea;
  cursor: pointer;
}

.pageLinks {
  -webkit-padding-start: 0;
